.grid {
  @apply .flex .flex-wrap .items-stretch .-mx-4;
}

.grid > div {
  @apply .w-full .px-4 .pb-8;
}

@screen sm {
  .grid {
    --columns: 2;

    display: grid;
    grid-gap: 2em 1em;
    grid-auto-flow: dense;
    grid-template-columns: repeat(var(--columns), 1fr);
  }

  .grid > div {
    @apply .w-auto .px-0 pb-0;
  }
}

@screen xl {
  .grid {
    --columns: 3;
  }
}
